/*@import url('https://fonts.googleapis.com/css?family=EB+Garamond:400,400i,500%7COpen+Sans:400,400i,600&amp;subset=cyrillic,cyrillic-ext,greek,greek-ext,latin-ext');*/
/*@font-face { font-family: 'shanticon'; src: url('shanticon.ttf?ueun15') format('truetype'); font-weight: normal; font-style: normal; }*/

html {
    font-size: small;
}

.sui-main {
    all: initial;
    position: absolute;
    left: 0;
    top: 0;
    line-height: normal;
    font-size: 1rem;
    box-sizing: initial;
    padding: 0;
    margin: 0;
    -moz-box-sizing: initial;
    -webkit-box-sizing: initial;
    width: 100vw;
    height: 100vh;
    font-family: 'Open Sans', 'Segoe UI', Tahoma, sans-serif, shanticon;
}
.sui-main b {
    font-weight: 600;
}
.sui-top {
    position: absolute;
    right: 12px;
    top: 7px;
    user-select: none;
    box-sizing: content-box;
}
.sui-topBar {
    height: 43px;
    width: 100%;
    background-color: #eee;
    box-sizing: content-box;
}
.sui-editBut {
    float: right;
    color: #fff;
    font-size: 24px;
    margin: -6px -42px 0 0;
    cursor: pointer;
}
.sui-editBut:hover {
    color: #ccc;
}

/* SEARCH BOX */

.sui-search1 {
    float: left;
    background-color: #fafafa;
    font-size: 16px;
    color: #ccc;
    height: 22px;
    width: 210px;
    padding: 6px 8px 0 8px;
    border-radius: 16px 0 0 16px;
    border-left: 1px solid #999;
    border-top: 1px solid #999;
    border-bottom: 1px solid #999;
    box-sizing: content-box;
}
.sui-search2 {
    float: left;
    background-color: #fafafa;
    color: #888;
    height: 17px;
    width: 175px;
    margin-top: -2px;
    margin-left: 2px;
    font-size: 14px;
    outline: none;
    border-radius: 0;
    border: 0;
    appearance: none;
    -webkit-appearance: none;
    box-shadow: none;
    box-sizing: content-box;
}
.sui-search3 {
    float: right;
    font-family: shanticon;
    font-size: 16px;
    color: #ccc;
    cursor: pointer;
    box-sizing: content-box;
}
.sui-search4 {
    display: inline-block;
    background-color: #ddd;
    font-size: 16px;
    color: #999;
    border-radius: 0 16px 16px 0;
    border: 1px solid #999;
    height: 23px;
    width: 27px;
    padding: 5px 0 0 7px;
    cursor: pointer;
    vertical-align: top;
    box-sizing: content-box;
    font-family: shanticon;
}
.sui-search4:hover {
    opacity: 0.66;
}
.sui-search5 {
    display: inline-block;
    font-size: 10px;
    cursor: pointer;
    color: #666;
    width: auto;
    text-align: center;
    padding: 5px 0 0 12px;
    line-height: 10px;
    width: 60px;
    text-transform: uppercase;
}
.sui-search5:hover {
    color: #668eec;
}

/* HAMBURGER MENU */

.sui-hamBut {
    display: inline-block;
    font-family: shanticon;
    font-size: 16px;
    cursor: pointer;
    color: #666;
    vertical-align: 2px;
    margin-left: 12px;
}
.sui-hamBut:hover {
    color: #668eec;
}
.sui-hamburger {
    position: absolute;
    font-size: 14px;
    background-color: #666;
    color: #fff;
    width: calc(100% - 12px);
    height: 38px;
    display: none;
    padding: 16px 12px 0 0;
}
.sui-hamItem {
    float: right;
    cursor: pointer;
    text-align: right;
    width: 150px;
}

.sui-header {
    height: 52px;
    width: 100%;
    user-select: none;
    background-color: #888;
    color: #fff;
}

.sui-contentHead {
    user-select: none;
    height: 100%;
    float: left;
    padding: 6px 12px 0 22px;
    font-size: 20px;
    line-height: 20px;
    width: 100%;
    max-width: calc(100% - 80px);
}
.sui-type {
    background-color: #fff;
    font-size: 12px;
    color: #666;
    border-radius: 8px;
    display: inline-block;
    width: 207px;
    border: 1px solid #999;
    height: 23px;
    line-height: 18px;
    cursor: pointer;
}
.sui-typeIcon {
    display: inline-block;
    border-radius: 8px 0 0 8px;
    width: 25px;
    height: 20px;
    padding-top: 3px;
    text-align: center;
    font-size: 14px;
    color: #fff;
    margin-right: 8px;
}
.sui-typeSet {
    float: right;
    border-radius: 0 8px 8px 0;
    width: 20px;
    height: 20px;
    cursor: pointer;
    padding-top: 3px;
    text-align: center;
    font-size: 18px;
    color: #666;
    background-color: #ddd;
}
.sui-typeSet:hover {
    opacity: 0.66;
}
.sui-typeList {
    position: absolute;
    left: calc(100% - 560px);
    top: 34px;
    background-color: #fff;
    font-size: 12px;
    color: #666;
    border-radius: 0 0 8px 8px;
    display: inline-block;
    width: 138px;
    cursor: pointer;
    border: 1px solid #999;
    padding: 12px;
    border: 1px solid #999;
}
.sui-typeItem:hover {
    background-color: #dee7f1;
}
.sui-content {
    position: absolute;
    left: 0;
    top: 43px;
    width: 100%;
    height: calc(100% - 73px);
    background-color: #fff;
    display: block;
    overflow-y: auto;
}
.sui-results {
    position: absolute;
    left: 0;
    top: 52;
    min-height: calc(100% - 77px);
    width: calc(100% - 24px);
    background-color: #ddd;
    overflow-x: hidden;
    padding: 12px;
}
.sui-resDisplay {
    cursor: pointer;
    display: inline-block;
    margin: 0 12px;
}
.sui-footer {
    position: absolute;
    left: 0;
    top: calc(100% - 31px);
    width: 100%;
    height: 25px;
    background-color: #888;
    color: #fff;
    text-align: center;
    padding-top: 6px;
    user-select: none;
    overflow: hidden;
}
.sui-infoBottom {
    position: absolute;
    left: 12px;
    height: 33%;
    width: 33%;
    top: calc(67% - 26px);
    display: none;
    min-width: 500px;
    color: #555;
    background-color: #f8f8f8;
    padding: 0 16px 16px 16px;
    border: 1px solid #999;
    border-radius: 8px 8px 0 0;
    overflow-y: auto;
}

/* LIST RESULTS */

.sui-item {
    border-top: 0.5px solid #cde3f7;
    margin-left: 30px;
    padding: 6px 0;
}
.sui-itemPlus {
    display: inline-block;
    color: #888;
    margin-left: -30px;
    cursor: pointer;
}
.sui-itemIcon {
    display: inline-block;
    color: #fff;
    margin-left: -30px;
    font-size: 13px;
    cursor: pointer;
    border-radius: 4px;
    text-align: center;
    width: 24px;
    height: 20px;
    margin-left: 20px;
    padding-top: 4px;
}
.sui-itemPic {
    width: 100px;
    cursor: pointer;
    outline: 4px solid #d7d7d7;
    outline-offset: 4px;
    border-radius: 4px;
    margin: 0 0 12px 8px;
}
.sui-itemInfo {
    display: inline-block;
    margin-left: 20px;
    vertical-align: top;
    line-height: 23px;
    font-size: 13px;
}
.sui-itemRelated {
    display: inline-block;
    font-family: serif;
    margin-left: 20px;
    line-height: 24px;
    color: #000;
}
.sui-itemTitle {
    display: inline-block;
    margin-left: 8px;
    cursor: pointer;
}
.sui-itemTitle:hover {
    color: #a4baec;
}
.sui-itemSubtitle {
    display: inline-block;
    margin-left: 8px;
}
.sui-itemFeature {
    display: inline-block;
    font-family: serif;
    font-style: italic;
    font-size: 12px;
}
.sui-itemTrail {
    font-style: italic;
    font-size: 11px;
    margin: 4px 0 4px 34px;
    cursor: pointer;
}
.sui-itemAncestor:hover {
    color: #a4baec;
}
.sui-itemId {
    float: right;
    font-size: 11px;
    font-style: italic;
    margin-top: -2px;
}
.sui-itemMore {
    padding: 12px 0 12px 34px;
    display: none;
    color: #444;
}

/* GRID RESULTS */

.sui-grid {
    display: inline-block;
    height: 100px;
    width: 150px;
    margin-bottom: 2px;
    cursor: pointer;
    overflow: hidden;
    border: 2px solid #ddd;
    text-align: center;
    background-color: #aaa;
}
.sui-grid:hover {
    border-color: #a4baec;
}
.sui-gridPic {
    height: 100%;
}
.sui-gridInfo {
    position: absolute;
    cursor: pointer;
    color: #fff;
    margin-left: 128px;
    margin-top: -24px;
    padding-top: 1px;
    text-align: center;
    text-shadow: 1px 1px 2px #666;
}
.sui-gridGlyph {
    position: absolute;
    font-size: 36px;
    pointer-events: none;
    width: 138px;
    height: 72px;
    margin-top: -100px;
    text-align: center;
    padding: 16px 12px 0 12px;
    overflow: hidden;
}
.sui-gridPopup {
    position: absolute;
    width: 300px;
    left: calc(50% - 250px);
    top: calc(50% - 50px);
    font-family: Verdana, Geneva, sans-serif, shanticon;
    padding: 16px;
    border-radius: 8px;
    background-color: #fff;
    border: 1px solid #ccc;
    display: none;
    font-size: 12px;
}

/* CARD RESULTS */

.sui-card {
    display: inline-block;
    border-top: 3px solid #9e894d;
    margin: 6px;
    background-color: #fff;
    width: 180px;
    height: 232px;
    vertical-align: top;
    overflow: hidden;
    border-radius: 0 0 5px 8px;
}
.sui-card:hover {
    border-top: 3px solid #a4baec;
}
.sui-cardPic {
    width: 180px;
    cursor: pointer;
}
.sui-cardInfo {
    padding: 10px;
    color: #666;
    font-size: 11px;
    line-height: 17px;
    margin-top: -6px;
    height: 92px;
    overflow: hidden;
}
.sui-cardGlyph {
    position: absolute;
    width: 180px;
    font-size: 36px;
    text-align: center;
    pointer-events: none;
    margin-top: -68px;
}
.sui-cardType {
    display: inline-block;
    margin-left: -23px;
    vertical-align: top;
    padding-top: 5px;
    color: #fff;
    text-shadow: 1px 1px 2px #666;
}
.sui-cardFooter {
    background-color: #9e894d;
    height: 21px;
    color: #fff;
    border-radius: 0 0 5px 8px;
    padding: 3px 0 0 8px;
}

/* ADVANCED SEARCH */

.sui-adv {
    position: absolute;
    left: calc(100% - 361px);
    top: 43px;
