}
.sui-advHeader:hover {
    color: #668eec;
}
.sui-advHeader {
    cursor: pointer;
    text-transform: uppercase;
}
.sui-advTermRem {
    display: inline-block;
    cursor: pointer;
    margin-right: 4px;
}
.sui-advValueRem:hover {
    color: #668eec;
}
.sui-advTerm {
    margin: 2px 0 2px 24px;
    font-size: 11px;
}
.sui-advEdit {
    display: none;
    border: 1px solid #ccc;
    width: calc(100% - 18px);
    background-color: #fff;
    text-align: left;
    border-radius: 5px;
    padding: 8px;
    font-size: 12px;
    color: #666;
    margin: 6px 0 6px 0;
}

.sui-advEdit input {
    width: 90px;
    border: 1px solid #999;
    border-radius: 12px;
    font-size: 11px;
    padding-left: 6px;
}

.sui-advPlus {
    float: right;
}

.sui-advEditBut {
    display: inline-block;
    cursor: pointer;
    vertical-align: -2px;
    color: #999;
}
.sui-advEditBut:hover {
    color: #a4baec;
}
.sui-advEditNums {
    float: right;
    font-size: 9px;
    padding-top: 5px;
}
.sui-advEditBool {
    display: inline-block;
    cursor: pointer;
    color: #668eec;
    border: 0.5px solid#999;
    border-radius: 2px;
    padding: 0 0 1px 3px;
    margin: 2px 0;
}
.sui-boolItem {
    display: inline-block;
    color: #999;
}
.sui-boolItem:hover {
    color: #668eec;
}
.sui-advEditList {
    overflow-x: hidden;
    max-height: 360px;
}
.sui-advEditLine {
    cursor: pointer;
    padding: 2px;
    border-radius: 5px;
}
.sui-advEditLine:hover {
    background-color: #dee7f1;
}

.sui-advEditLine.chosen {
    background-color: #dcdcdc;
}

.sui-advViewListPage {
    display: inline-block;
}
.sui-advViewListPage:hover {
    color: #668eec;
}
.sui-advViewTreePage {
    display: inline-block;
    padding: 0 4px;
    color: #777;
}
.sui-advViewTreePage:hover {
    color: #a4baec;
}
.sui-adv-facetlist {
    max-height: 15em;
}
.sui-geoLocate {
    background-color: #6faaf1;
    border-radius: 16px;
    margin-top: 4px;
    padding: 0 12px;
    border: 1px solid #999;
    cursor: pointer;
    text-align: center;
    float: right;
    color: #fff;
}
/* TREE */
.sui-tree {
    user-select: none;
    overflow-x: hidden;
    max-height: 360px;
    padding-left: 6px;
    font-size: 12px;
}
.sui-tree ul {
    list-style: none outside none;
    padding: 0;
    margin: 0;
}
.sui-tree li a {
    line-height: 19px;
    cursor: pointer;
}
.sui-tree > ul > li > a {
    color: #3b4c56;
    display: block;
    font-weight: normal;
    position: relative;
    text-decoration: none;
}
.sui-tree li.parent > a {
    padding: 0 0 2px 20px;
}
.sui-tree li.parent > a:before {
    background-image: url('treebuts.png');
    content: '';
    display: block;
    height: 15px;
    left: 0;
    position: absolute;
    top: 2px;
    width: 15px;
    background-position: 0;
}
.sui-tree ul li.active > a:before {
    background-position: 30px;
}
.sui-tree ul li ul {
    border-left: 1px solid #a4baec;
    display: none;
    margin: 0 0 0 7px;
    padding: 0 0 0 12px;
}
.sui-tree ul li ul li {
    position: relative;
}

/* PLACES */
#plc-main {
    width: calc(100% - 193px);
    margin-left: 192px;
}
#plc-infoDiv {
    position: absolute;
    top: 292px;
    left: 219px;
}
#plc-infoDiv input {
    border: none;
    box-shadow: rgba(0, 0, 0, 0.3) 0px 1px 2px;
}

/* RELATED */
.sui-related {
