@media screen and (min-width: 992px) {
    /* testing these two rulesets is for positioning book reader height */
    .astviewer.texts article.main-col {
        position: relative;
        padding-top: 1.7rem;
    }
    .astviewer.texts article.main-col > div {
        min-height: 580px;
    }

    .astviewer.texts #shanti-texts-container {
        /*height:auto;
      position: absolute;*/
        top: 5rem;
        bottom: 0.1rem;
        padding: 0 2rem 0 0;
        margin-bottom: 0;
    }
    .has-tabs.astviewer.texts #shanti-texts-container {
        position: absolute;
        top: 0.5rem;
    }

    #shanti-texts-sidebar {
        height: 100%;
        padding: 0;
        width: 32%;
    }

    #shanti-texts-body {
        margin-right: 1.25rem;
        padding: 3.5rem 1.5rem 0 3.25rem;
    }

    #shanti-texts-sidebar .tab-content {
        position: absolute;
        top: 38px;
        bottom: 0;
        max-height: 100%;
        border: 1px solid #ddd;
        border-right: 1px solid #d0d0d0;
        border-bottom: 1px solid #aaa;
        border-top: none;
    }

    #shanti-texts-sidebar .tab-content::-webkit-scrollbar {
        height: 0.7rem; /* horizontal scrolling scroll handle */
        width: 1.15rem; /* vertical scrolling scroll handle */
    }

    .astviewer.texts .nav-tabs.nav-justified > a {
        border-right: 1px solid #ddd;
        border-top: 2px solid #aaa;
        border-bottom: 2px solid #ddd;
        background-color: #f5f5f5 !important;
        font-size: 1em;
    }

    .astviewer.texts .nav-tabs.nav-justified > a:last-child {
        border-right: 2px solid #ddd;
    }
    .astviewer.texts .nav-tabs.nav-justified > a {
        border-radius: 0;
        background: #f9f9f9 !important;
        height: 3.4rem;
        color: #555;
        font-size: 0.88em;
        line-height: 1.25em;
        padding-top: 1.2rem;
        -webkit-transition: all 50ms ease 0s;
        transition: all 50ms ease 0s;
        display: block;
    }
    .astviewer.texts .nav-tabs.nav-justified > a:hover {
        background-color: #fff !important;
        color: #777;
        box-shadow: none;
    }

    .astviewer.texts .nav-tabs.nav-justified > a.active {
        border-top: none;
        box-shadow: none;
        border-right: 1px solid #ddd;
        border-top: 2px solid #8c48a1;
        border-bottom: none;
        background-color: #fff !important;
        color: #555;
        box-shadow: none;
        display: block;
        font-size: 0.93em;
        position: relative;
        background-color: #fff !important;
        letter-spacing: -0.013rem;
    }
    .astviewer.texts .nav-tabs.nav-justified > a:hover:not(.active) {
        box-shadow: none;
        border-top: 2px solid #888;
    }
    .astviewer.texts .nav-tabs.nav-justified > a.active {
    }
    .astviewer.texts .nav-tabs.nav-justified > a.active:focus,
    .astviewer.texts .nav-tabs.nav-justified > a.active:hover {
        background-color: #fff;
        color: #444;
        border-bottom-color: #fff !important;
        box-shadow: none;
    }
    .astviewer.texts .nav-tabs.nav-justified > a.active:last-child {
    }
    .page-shanti-texts .nav-tabs.nav-justified > a.active::before {
        display: none; /* remove the active-tab-pointer icon */
    }
} /* END (min-width: 992px) */

.nav-tabs.nav-justified > a {
    position: relative;
    top: -0.4rem;
}

.nav-tabs.nav-justified > a {
    font-size: 0.93em !important;
    text-transform: uppercase;
    line-height: 1.32em;
    padding-top: 1.5rem;
}

.nav-tabs.nav-justified > a.active::before {
    color: #555 !important;
}

